import Vue    from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import Banco from '@pages/General/Banco/BancoList.vue'
import Page from  '@pages/page/Page.vue';


export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
      routes: [
        {
          path: '/banco',
          name: 'banco',
          icon: 'bubble_chart',
          component: Banco,
        
        },
        {
          path: '/page',
          name: 'page',
          icon: 'bubble_chart',
          component: Banco,
        
        },
    ]
})


