<template>
<div>

    <v-row>

        <v-col xs="2" class="mx-3" id="listComercios"> 
            <v-chip label class="purple--text mr-1">
                <v-icon class="purple--text mr-1">mdi-storefront</v-icon> 
                 {{countComercios}} Comercios Listados 
            </v-chip>
        </v-col>

        <v-col xs="10" >
            <v-chip class="my-1" close small dark color="purple" @click:close="categoria = null">
                <v-avatar left>
                    <v-icon class="mx-2">{{ getCategoriaById(categoria).icon }}</v-icon>
                </v-avatar>
                 {{ getCategoriaById(categoria).nb_categoria }} 
            </v-chip> 
        </v-col>
        
    </v-row>

    <v-divider></v-divider>

    <v-row no-gutters> 
        
        <v-col :lg="cols.lg" :md="cols.md" :sm="cols.sm" :xs="cols.xs" v-for="(comercio, i) in comerciosFiltered" :key="i" >
                
            <app-comercio :comercio="comercio"></app-comercio>

        </v-col>

    </v-row>

</div>
</template>

<script>

import { mapGetters } from 'vuex';
import AppComercio from '~/components/comercio/AppComercio';
import { comercios as comerciosJSON }  from '~/assets/data/comercios.json'

export default 
{
    components: { 
        'app-comercio': AppComercio, 
    },
    props:
    {
        filtros: {
            type:       Object,
            default:    () =>  {  return { nombre: null, categoria: null } }
        },
        cols: {
            type:       Object,
            default:    () =>  {  return { lg: 4, md: 4, sm: 6, xs: 12 } }
        }
    },
    mounted()
    {
        this.$vuetify.goTo('#listComercios', { duration: 2000, offset: 5 } )
    },
    computed: 
    {
        ...mapGetters(['getCategoriaById']),

        categoria:
        {
            get() {
                return this.$store.getters['getCategoria']
            },
            set(categoria) {
                this.$store.commit('setCategoria', categoria )
            }
        },

        comerciosFiltered()
        {
            const comercios = [];

            if ( this.filtros.nombre || this.filtros.categoria  )
            {
                if (this.filtros.categoria)
                {
                    for (const comercio of this.comercios) {

                        if(comercio.categoria_id == this.filtros.categoria)
                        {
                            if(this.filtros.nombre) 
                            {
                               if(comercio.tx_nombre.toLowerCase().includes(this.filtros.nombre.toLowerCase()) >= 0) {
                                    comercios.push(comercio); 
                               }
                               
                            }else
                            {
                               comercios.push(comercio);
                            }
                        }
                    }
                }
            }
            return comercios;
        },

        countComercios()
        {
            return this.comerciosFiltered.length
        }
    },
    data () 
	{
        return {
            comercios: comerciosJSON
        }
    },
}
</script>

